<script>
import {mapState} from "vuex";

export default {
  name: "TeachersListDashboard",
  data() {
    return {
      tableHeaders: [
        {text: 'Name', value: 'name'},
        {text: 'Actions', value: 'actions', sortable: false}
      ],
      tableLoading: false
    }
  },
  computed: {
    ...mapState(['teachers'])
  },

  mounted() {
    this.tableLoading = true;
    this.$store.dispatch('fetchTeachers')
        .then(() => {
          this.tableLoading = false;
        })
  }
}
</script>

<template>
  <v-container>
    <router-view></router-view>

    <v-data-table
        :headers="tableHeaders"
        :items="teachers"
        sort-by="name"
    >
      <template v-slot:top>
        <v-toolbar flat>
          <v-toolbar-title>Teachers</v-toolbar-title>
          <v-divider
              class="mx-4"
              inset
              vertical
          ></v-divider>
          <v-spacer/>
          <v-btn
              color="primary"
              @click="$router.push('/teacher/new')"
          >Add Teacher
          </v-btn>
        </v-toolbar>
      </template>
      <template v-slot:[`item.actions`]="{ item }">
        <v-icon
            @click="$router.push('/teacher/edit/' + item.id)"
        >
          mdi-pencil
        </v-icon>
        <v-icon
            @click="$router.push('/teacher/delete/' + item.id)"
        >
          mdi-delete
        </v-icon>
      </template>
    </v-data-table>
  </v-container>
</template>

